* {
  box-sizing: border-box;
  padding: 0;
  list-style: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #96d7da;
  margin: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  color: #090800;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #130c0b;
  border-radius: 4px;
  margin-bottom: 30px;
  padding: 8px;
  font-weight: bold;
}

.country-list {
  flex-direction: column;
  row-gap: 20px;
  margin: 0;
  padding: 0;
  display: flex;
}

.country-list-item {
  align-items: center;
  gap: 5px;
  display: flex;
}

.country-flag {
  width: 50px;
}

.country-name {
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 1.1em;
  font-weight: 500;
}

.country-info {
  width: 500px;
  height: 300px;
  flex-direction: column;
  display: flex;
  position: relative;
}

.country-info > .country-name {
  color: #000;
  text-align: center;
  margin: 40px 0 25px;
  font-size: 2em;
  font-weight: 900;
  text-decoration: underline;
}

.country-info .country-flag {
  object-fit: scale-down;
  width: 500px;
  height: 300px;
  object-fit: cover;
  opacity: .1;
  filter: blur(2px);
  z-index: -1;
  position: absolute;
}

.country-info-list {
  color: #000;
  flex-direction: column;
  gap: 10px;
  margin: 0;
  padding-left: 10px;
  font-size: 1.2em;
  font-weight: 700;
  display: flex;
}

.country-info-list p {
  margin: 0;
}

.country-desc {
  text-transform: uppercase;
  color: #273030;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
  font-size: .7em;
}

